import "./styles.css";
import React, { useState } from "react";

var listOfBooks = {
  javascript: [
    { name: "Complete reference to Javascript", rating: "4/5" },
    { name: "Eloquent Javascript", rating: "4.6/5" },
    { name: "You don't know JS", rating: "5/5" }
  ],

  python: [
    { name: "Hard way to learn Python", rating: "4.5/5" },
    { name: "Complete Reference Python", rating: "4.7/5" },
    { name: "Python in 24 hours", rating: "4.5/5" }
  ],

  philosophy: [
    { name: "Journey of Life", rating: "4.8/5" },
    { name: "Life of Pie", rating: "4.9/5" },
    { name: "Life of a Monk", rating: "4.95/5" }
  ],

  writing: [
    { name: "On Writing well", rating: "5/5" },
    { name: "On Advertising", rating: "4/5" },
    { name: "Cashvertising", rating: "5/5" }
  ]
};

export default function App() {
  const [selectedTopic, setTopic] = useState("javascript");

  function topicSelectionHandler(genre) {
    setTopic(genre);
  }

  return (
    <div className="App">
      <h1
        style={{
          textAlign: "center",
          color: "blue"
        }}
      >
        <span>ðŸ“–</span> Goodbooks Recommendations
      </h1>

      <div style={{ margin: "1rem" }}>
        Read book recommendations for your favorite topic!
      </div>

      <hr />

      {/* generating buttons - for each item in the object create a button*/}
      <div>
        {Object.keys(listOfBooks).map((topic) => (
          <button
            key={topic.id}
            onClick={() => topicSelectionHandler(topic)}
            style={{ margin: "0.5rem" }}
          >
            {topic}
          </button>
        ))}
      </div>

      {/* generating list  */}
      <div style={{ margin: "auto" }}>
        {listOfBooks[selectedTopic].map((books) => (
          <ul style={{ textAlign: "left" }}>
            <li
              key={books.name}
              style={{
                paddingInlineStart: "1rem",
                listStyle: "none",
                border: "0.2px solid grey",
                borderRadius: "0.5rem",
                padding: "1rem",
                width: "60%"
              }}
            >
              <div style={{ fontSize: "larger" }}> {books.name} </div>
              <div style={{ fontSize: "smaller" }}> {books.rating} </div>
            </li>
          </ul>
        ))}
      </div>
    </div>
  );
}
